.pannel-left {
    visibility: hidden;
    min-width: 200px;
    margin: 40px 24px 40px 40px;
    text-align: right;
    position: fixed;
    top:0;
    left: 0;
}
.pannel-right {
    width: 100%;
    text-align: center;
    margin: 150px 40px;
}

.letter-left {
    -webkit-writing-mode: vertical-rl;
        -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
    position: fixed;
    left: 20px;
    top:calc(39%);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.letter-right {
    -webkit-writing-mode: vertical-rl;
        -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
    position: fixed;
    right: 20px;
    top:calc(39%);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

}
.header-ring {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.8);
    /* background-color: #07FF47; */
}
.header-ring marquee {
    width: 100%;
    /* color: black; */
    position: fixed;
    letter-spacing: 0.1rem;
}
.emojis {
    position: fixed;
    bottom: 0px;
    left: 30px;
    display: none;
}
.emojis img {
    width: 100px;
    transform: rotate(10deg);
}


.nav {
    display: inline-block;
    margin-bottom: 50px;
}
.thumb-images {
    display: flex;
    flex-wrap: wrap;
    align-items:flex-end;
    margin-bottom: 10px;
    text-align: center;
    max-width: 100vw;
}
.thumb-images img {
    width: 700px;
    text-align: center;
    margin-bottom: 50px;
}
.thumb-image {
    width: 700px;
    margin: 0 auto 100px auto;
    /* margin-bottom: 100px; */ 
}
.nametag {
    /* border: 1px solid rgba(255, 255, 255, 0.7); */
    display: inline-block;
    vertical-align: bottom;
    padding: 10px;
}
