.oval {
    /* background-image: url("/pf/oval.png");
    background-size: contain; */
    width: 200px;
    height: 50px;
    position: fixed;
    border: 1px solid #5391FF;
    color: #5391FF;
    border-radius: 50%;
    text-align: center;
    bottom:40px;
    right: 50px;
    transform: rotate(-8deg);

}
.oval:hover {
    bottom:45px;
    right:55px;
    cursor: pointer;
}
.object, .object img {
    /* height: 60px; */
    height: 0;
}
.object:hover {
    cursor: all-scroll;
}
.drag-wrapper {
    margin:0;
    padding: 0;
    position: fixed;
}
.section-images img {
    /* max-width: 1000px; */
    width: 700px;
    margin-bottom: 100px;
}
.selected p {
    color: #5391FF;
}

