.bt-basic {
    transition-duration: 0.2s;
    background-color: #fff;
    /* font-family: "Montserrat", "Nanum Gothic", sans-serif; */
    border: 3px solid #000;
    height: 82px;
    padding: 0 20px;
    font-size: 24px;
    font-weight: bold;
}

.bt-blue {
    background-color: #fff;
    color : #0D45BB;
    /* font-family: "Montserrat", "Nanum Gothic", sans-serif; */
    border: 3px solid #0D45BB;
    height: 82px;
    padding: 0 20px;
    font-size: 24px;
    font-weight: bold;
}

.bt-blue:hover {
    transition: background-color 0.5s, color 0.5s linear;

    cursor: pointer;

    background-color: #0D45BB;
    color: #fff;
    border: 3px solid #0D45BB;
}

.seojimin, .seojimin img {
    position:fixed;
    bottom:40%;
    left:40%;
    z-index: -1;
    width: 300px;
}

.seojimin2, .seojimin2 img {
    position:fixed;
    bottom:40%;
    left:40%;
    width: 300px;
}

@media (max-width:768px) {

    .pannel-right {
        margin: 150px 0px;
    }
    .thumb-images img {
        width: 380px;
        text-align: center;
    }
    .thumb-image {
        width: 380px;

    }
    .thumb-images {
        display: inline-block;
    }
    .section-images img {
        /* max-width: 1000px; */
        width: 380px;
        margin-bottom: 100px;
    }

    .seojimin, .seojimin img {
        position:fixed;
        bottom:40%;
        left:25%;
        z-index: -1;
        width: 200px;
    }
}



.window {
    border-radius: 4px;
    box-shadow: 0 4px 12px rgba(0,0,0,.5);
    width: 800px;
    /* margin:auto;
    margin-top:20px; */
    margin: 100px auto;
    overflow:hidden;
    border:1px solid #C1C2C2;
    /* position: fixed; */
  }
  
  .inside {
    background:#fff;
    overflow:hidden;
    /* padding:10px; */
    padding-bottom:-5px;
  }
  .inside .blok {
    width:32%;float:left;margin-right:1.5%;
    height:50px;
    background:#efefef;
  }
  .top {
    padding:0.3646vw 0;
    position:relative;
    background: #f1f1f1;
    background: -webkit-gradient(left top, left bottom,color-stop(3%, #E9E9E9), color-stop(100%, #d8d8d8));
    background: linear-gradient(to bottom, #E9E9E9 3%, #d8d8d8 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f1f1f1', endColorstr='#d8d8d8', GradientType=0 );
    box-shadow:         inset 0px 1px 1px 0px rgba(255, 255, 255, 0.76);  
    
    overflow:hidden;
    border-bottom:2px solid #BDBCC1;
  }
  
  .top > div {
    float:left;
  }
  
  
  .panel {
    padding-left:0.4688vw;
    padding-top:0.1042vw;
  }
  
  .panel > span {
    display:inline-block;
    float:left;
    width:0.6250vw;
    height:0.6250vw;
    margin-right:0.3646vw;
    border-radius: 6px;  
    cursor:pointer;
  
  }
  
  .panel span.first {
    background:#FF5F4F;
  }
  
  .panel span.second {
    background:#F9C206;
  }
  
  .panel span.third {
    background:#19CC32;
  }
  
body {
    font-family: "Castoro", serif;
    letter-spacing: -0.015em;
  }

  .ff-karla {
    font-family: "Karla", sans-serif;
    letter-spacing: -0.015em;
  }
  .ff-castoro {
    font-family: "Castoro", serif;
    letter-spacing: -0.015em;
  }
  
  /* Font Size */
  
  .fs-tiny {
    font-size: 12px;
    line-height: 1.3333333333;
  }
  
  .fs-small {
    font-size: 14px;
    line-height: 1.4285714286;
  }
  
  .fs-base {
    font-size: 16px;
    line-height: 1.5;
  }
  
  .fs-medium {
    font-size: 18px;
    line-height: 1.5555555556;
  }
  
  .fs-large {
    font-size: 20px;
    line-height: 1.6;
  }
  
  .fs-h2 {
    font-size: 35px;
    line-height: 2.7714285714;
  }

  .fs-h3 {
    font-size: 24px;
    line-height: 2.3333333333;
  }
  
  .fs-h1 {
    font-size: 50px;
    line-height: 1.62;
  }
  
  /* Font Weight */
  
  .fw-light,
  .fw-300 {
    font-weight: 300;
  }
  
  .fw-regular,
  .fw-400 {
    font-weight: 400;
  }
  
  .fw-medium,
  .fw-500 {
    font-weight: 500;
  }
  
  .fw-bold,
  .fw-700 {
    font-weight: 700;
  }
  
  /* Color */
  
  .text-dark {
    color: #1f2d3d;
  }
  
  .text-primary {
    color: #0D45BB;
  }
  
  .text-secondary {
    color: #F3641D;
  }
  
  .text-tertiary {
    color: #c0ccda;
  }
  .text-grey {
    color: rgba(0,0,0,0.37);
  }
  
  .text-white {
    color: #fff;
  }
  
  .text-success {
    color: #13ce66;
  }
  
  .text-error {
    color: #ff5216;
  }
  
  .text-info {
    color: #009eeb;
  }
